/* stylelint-disable */
html {
  box-sizing: border-box;
}

ul,
p,
h3,
h2 {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  border: none;
}

.btn-reset {
  border: none;
  background: #E9E9DB;
  border-radius: 40px;
  font-size: 12px;
  line-height: 18px;
  font-weight: var(--font-r);
  height: 46px;
  padding: 0px 20px;
  width: 100%;
  cursor: pointer;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  font-weight: var(--font-r);
  font-size: 16px;
  line-height: 20px;
  color: #202217;
  background: #E9E9DB;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: #202217;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  margin: 0 auto;
  max-width: 1250px;
}

.header {
  background-color: #E9E9DB;
  color: #202217;
}

.navigation {
  padding: 25px 0;
  display: flex;
  justify-content: space-between;
  max-width: 975px;
  margin: 0 0 27px auto;
  border-bottom: 1px solid #202217;
}
.navigation__item {
  display: flex;
  gap: 48px;
  justify-content: space-between;
}
.navigation__item a {
  transition: 0.4s;
  color: #202217;
}
.navigation__item a:hover {
  color: #D2564F;
}

.main-banner {
  display: flex;
  justify-content: flex-end;
  position: relative;
}
.main-banner__img-text-top {
  position: absolute;
  top: 30px;
  left: 260px;
}
.main-banner__img-text-down {
  position: absolute;
  bottom: 130px;
  right: -1px;
}
.main-banner__img-left {
  position: absolute;
  top: -50px;
  left: -135px;
}

.section-2__block {
  margin-top: 100px;
}
.section-2__title {
  position: relative;
  z-index: 2;
}
.section-2__content {
  display: flex;
}
.section-2__text {
  width: 40%;
  margin-top: 40px;
  padding-left: 10px;
}
.section-2__text h2 {
  margin-bottom: 15px;
}
.section-2__text p {
  max-width: 80%;
  font-size: 0.8rem;
  line-height: 1rem;
}
.section-2__text p:not(:last-child) {
  margin-bottom: 50px;
}
.section-2__images {
  display: flex;
  width: 60%;
  position: relative;
}
.section-2__images img {
  max-width: 370px;
}
.section-2__images .section-2--img {
  margin-top: 40px;
  position: relative;
  z-index: 1;
}
.section-2__images .section-2--img-absolute {
  position: absolute;
  width: 100%;
  max-width: 378px;
  top: -160px;
  right: 60px;
  z-index: 1;
}
.section-2__images .section-2--lines {
  position: absolute;
  top: 50px;
  right: 127px;
  z-index: 0;
}